import MgrSdkCore from "./MgrSdkCore";
import MgrsdkcoreH5 from "./MgrSdkCoreH5";

/**
 * 
 */
class MgrSdk {

    /**
     * 
     */
    core: MgrSdkCore;

    /**
     * 
     */
    init () {
        this.core = new MgrsdkcoreH5 ();
        return Promise.resolve ();
    }
}

namespace MgrSdk {
    /**
     * 
     */
    export const inst = new MgrSdk ();
}

export default MgrSdk;