import JWebglProgram from "./JWebglProgram";

/**
 * 
 */
export default abstract class JWebglProgramVarying {

    /**
     * 
     */
    relProgram: JWebglProgram;

    /**
     * 
     */
    name: string;

    constructor (args: {
        program: JWebglProgram,
        name: string
    })
    {
        this.relProgram = args.program;
        this.name = args.name;
    }

    /**
     * 
     */
    abstract onGetDefine (): string;

    toString () {
        return this.name;
    }
}